import React from 'react'
import {
    BrowserRouter as Router,
    Switch,
    Route,
  } from "react-router-dom";
import { Home,Categories,Search,SingleProduct,Favorites } from './screens/Index';
import {Header, Footer} from './components/Index';


export default function App() {
    return (
        <Router basename='/'>
            <Header/>
            <Switch>
                <Route exact path="/">
                    <Home />
                </Route>
                <Route path="/cateogries">
                    <Categories />
                </Route>
                <Route path="single/:id">
                    <SingleProduct />
                </Route>
                <Route path="/favorites">
                    <Favorites />
                </Route>
                <Route path="/search">
                    <Search />
                </Route>
            </Switch>
            <Footer/>
        </Router>
    )
}
